import React, { Fragment } from "react";
import { withStateHandlers, withHandlers } from "recompose";

import { Row, Column } from "components/Grid.tsx";
import arrowDown from "assets/img/icons/black/light-fill/arrow-down.svg";

import {
  Container,
  Title,
  Subtitle,
  CenteredContainer,
  Breakdown,
  BreakdownRow,
  BreakdownCell,
  OverflowWrapper,
  ScrollButton,
  ScrollButtonImage,
  // mobile
  MobileRow,
  MobileTitle,
  TitleText,
  MobileWrapper,
  Icon,
  ProductTitle,
  ProductDescription,
  ExpansionWrapper,
} from "./elements";

import { data } from "./messages.jsx";

const scrollableBreakdownRef = React.createRef();

const _Desktop = ({ scrollRight }) => (
  <OverflowWrapper>
    <ScrollButton onClick={scrollRight}>
      <ScrollButtonImage src={arrowDown} />
    </ScrollButton>
    <Breakdown>
      <BreakdownRow isTopRow>
        <BreakdownCell isHeader index />
        <BreakdownCell isHeader isLabel />
        <BreakdownCell isHeader isHighlighted isExtendedTop>
          Brex Exclusive
        </BreakdownCell>
      </BreakdownRow>
      {data.map((item, i) => (
        <BreakdownRow key={`row-${i + 1}`}>
          <BreakdownCell index>{`0${i + 1}`}</BreakdownCell>
          <BreakdownCell isLabel>{item.prop}</BreakdownCell>
          <BreakdownCell isHighlighted>{item.brex}</BreakdownCell>
        </BreakdownRow>
      ))}

      <BreakdownRow isBottomRow>
        <BreakdownCell index />
        <BreakdownCell isLabel />
        <BreakdownCell isHighlighted isExtendedBottom />
      </BreakdownRow>
    </Breakdown>
    <Breakdown ref={scrollableBreakdownRef} scroll>
      <BreakdownRow isTopRow>
        <BreakdownCell isHeader>Amex SimplyCash Plus</BreakdownCell>
        <BreakdownCell isHeader>Amex Business Gold Rewards</BreakdownCell>
        <BreakdownCell isHeader>Amazon Business Prime (Amex)</BreakdownCell>
        <BreakdownCell isHeader>Chase Ink Business Unlimited</BreakdownCell>
        <BreakdownCell isHeader>Chase Ink Business Preferred</BreakdownCell>
        <BreakdownCell isHeader>Capital One Spark Cash/Miles</BreakdownCell>
      </BreakdownRow>
      {data.map((item, i) => (
        <BreakdownRow key={`row-${i + 1}`}>
          <BreakdownCell>{item.amexSimplyCash}</BreakdownCell>
          <BreakdownCell>{item.amexBusinessGold}</BreakdownCell>
          <BreakdownCell>{item.amazonBusiness}</BreakdownCell>
          <BreakdownCell>{item.chaseInkUnlimited}</BreakdownCell>
          <BreakdownCell>{item.chaseInkPreferred}</BreakdownCell>
          <BreakdownCell>{item.capitalOne}</BreakdownCell>
        </BreakdownRow>
      ))}

      <BreakdownRow isBottomRow>
        <BreakdownCell />
        <BreakdownCell />
        <BreakdownCell />
        <BreakdownCell />
        <BreakdownCell />
        <BreakdownCell />
      </BreakdownRow>
    </Breakdown>
  </OverflowWrapper>
);

const Desktop = withHandlers({
  scrollRight: () => () => {
    let length = 10;
    let slider;

    const interval = () => {
      if (length <= 0) window.clearInterval(slider);
      scrollableBreakdownRef.current.scrollLeft += 55;
      length -= 1;
    };

    slider = setInterval(interval, 10);
  },
})(_Desktop);

// MOBILE VERSION BELOW: Currently not exporting it, but will do so soon with a
// simple toggle based on screen width breakpoints. The component isn't perfect yet
// either

const _Category = ({ category, index, open, toggleOpenState }) => (
  <MobileRow>
    <MobileTitle onClick={() => toggleOpenState()}>
      <Icon alt="arrow down" src={arrowDown} />
      <TitleText>{`0${index + 1}`}</TitleText>
      <TitleText primary>{category.prop}</TitleText>
    </MobileTitle>
    {open && (
      <ExpansionWrapper>
        <ProductTitle>Brex</ProductTitle>
        <ProductDescription>{category.brex}</ProductDescription>
        <ProductTitle>Amex SimplyCash Plus</ProductTitle>
        <ProductDescription>{category.amexSimplyCash}</ProductDescription>
        <ProductTitle>Amex Business Gold Rewards</ProductTitle>
        <ProductDescription>{category.amexBusinessGold}</ProductDescription>
        <ProductTitle>Chase Ink Business Unlimited</ProductTitle>
        <ProductDescription>{category.chaseInkUnlimited}</ProductDescription>
        <ProductTitle>Chase Ink Business Preferred</ProductTitle>
        <ProductDescription>{category.chaseInkPreferred}</ProductDescription>
        <ProductTitle>Capital One Spark Cash/Miles</ProductTitle>
        <ProductDescription>{category.capitalOne}</ProductDescription>
      </ExpansionWrapper>
    )}
  </MobileRow>
);

const Category = withStateHandlers(
  { open: false },
  {
    toggleOpenState: ({ open }) => () => ({
      open: !open,
    }),
  },
)(_Category);

const Mobile = () => (
  <MobileWrapper>
    {data.map((category, index) => (
      <Category category={category} index={index} key={`category-${index}`} />
    ))}
  </MobileWrapper>
);

const BreakdownComponent = () => (
  <Fragment>
    <Container id="breakdown">
      <Row padding={30} gutterWidth={26}>
        <Column span={12}>
          <CenteredContainer>
            <Title>Compare Brex Rewards</Title>
            <Subtitle>
              Built on rigorous spend analysis, Brex Rewards help companies
              scale better than any other rewards program on the market.
            </Subtitle>
            <Desktop />
          </CenteredContainer>
        </Column>
      </Row>
    </Container>
    <Mobile />
  </Fragment>
);

export default BreakdownComponent;
