import React from "react";
import { compose, withStateHandlers, withHandlers } from "recompose";
import { clamp, round, reduce } from "lodash";

import OutboundLink from "components/reusable/atoms/links/OutboundLink";
import { Row, Column } from "components/Grid.tsx";
import { formatDollars } from "utils/formatting";
import Slider from "components/rewards/Slider";
import BudgetInput from "components/rewards/calculator/BudgetInput";

import {
  Wrapper,
  HeaderWrapper,
  HeaderText,
  TextAreaWrapper,
  MonthlyLabel,
  AnnualLabel,
  MonthlyCashback,
  AnnualCashbackWrapper,
  AnnualCashback,
  Underline,
  StyledLink,
  ValuePropBar,
  Bold,
  ValuePropWrapper,
} from "./elements";

const valueProps = [
  { title: "Unlimited points", body: <span>No cap on what you can earn</span> },
  {
    title: "Extra perks",
    body: (
      <span>
        Enjoy exclusive{" "}
        {
          // hash links need to use anchor tags because Gatsby
          // doesn't support them well
        }
        <OutboundLink href="#partneroffers">partner offers</OutboundLink>
      </span>
    ),
  },
  {
    title: "Redeem",
    body: (
      <span>
        Get statement credit or book with{" "}
        <StyledLink to="/travel">Brex Travel</StyledLink>
      </span>
    ),
  },
];

const _Calculator = ({
  monthlyBudget,
  setMonthlyBudget,
  sliders,
  updateSliders,
  getMonthlyCashback,
  toggleAdjustable,
}) => (
  <Wrapper>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanSmall={0} />
      <Column span={5} spanMedium={10} spanSmall={12}>
        <HeaderWrapper marginTop={-51}>
          <HeaderText>Your spend</HeaderText>
          <BudgetInput
            monthlyBudget={monthlyBudget}
            setMonthlyBudget={setMonthlyBudget}
          />
        </HeaderWrapper>
        {sliders.map((slider, index) => (
          <Slider
            category={slider.categoryName}
            multiple={slider.multiple}
            percentage={slider.percentage}
            adjustable={slider.adjustable}
            monthlyBudget={monthlyBudget}
            updateSliders={updateSliders}
            toggleAdjustable={toggleAdjustable}
            index={index}
            key={`key-${slider.categoryName}`}
          />
        ))}
      </Column>
      <Column span={1} spanSmall={0} />
      <Column span={0} spanMedium={1} spanSmall={0} />
      <Column span={4} spanMedium={10} spanSmall={12}>
        <HeaderWrapper>
          <HeaderText>Back in rewards</HeaderText>
          <TextAreaWrapper>
            <MonthlyLabel>MONTHLY</MonthlyLabel>
            <MonthlyCashback>
              {formatDollars(round(getMonthlyCashback()))}
            </MonthlyCashback>
          </TextAreaWrapper>
        </HeaderWrapper>
        <AnnualCashbackWrapper>
          <AnnualLabel>ANNUALLY</AnnualLabel>
          <AnnualCashback>
            {formatDollars(12 * round(getMonthlyCashback()))}
          </AnnualCashback>
          <Underline />
          <ValuePropWrapper>
            {valueProps.map((valueProp) => (
              <React.Fragment key={`key-${valueProp.title}`}>
                <ValuePropBar />
                <Bold>{`${valueProp.title}: `}</Bold>
                {valueProp.body}
              </React.Fragment>
            ))}
          </ValuePropWrapper>
        </AnnualCashbackWrapper>
      </Column>
      <Column span={1} spanSmall={0} />
    </Row>
  </Wrapper>
);

const slidersInitialState = [
  {
    categoryName: "LAB SUPPLIES",
    multiple: 2,
    percentage: 25,
    adjustable: true,
  },
  {
    categoryName: "CONFERENCE TICKETS",
    multiple: 7,
    percentage: 25,
    adjustable: true,
  },
  {
    categoryName: "BREX TRAVEL",
    multiple: 4,
    percentage: 25,
    adjustable: true,
  },
  {
    categoryName: "EVERYTHING ELSE",
    multiple: 1,
    percentage: 25,
    adjustable: true,
  },
];

export default compose(
  withStateHandlers(
    {
      monthlyBudget: 10000,
      sliders: slidersInitialState,
    },
    {
      setMonthlyBudget: () => (value) => ({ monthlyBudget: value }),
      toggleAdjustable: ({ sliders }) => (modifiedIndex) => ({
        sliders: sliders.map((slider, index) => ({
          ...slider,
          adjustable:
            index === modifiedIndex ? !slider.adjustable : slider.adjustable,
        })),
      }),
      updateSliders: ({ sliders }) => (
        modifiedSliderIndex,
        percentageDelta,
      ) => {
        // the max percentage that we have leftover after removing
        // all nonadjustable percentages
        const maxPercentage = sliders.reduce((maxTotal, slider) => {
          if (!slider.adjustable) return maxTotal - slider.percentage;
          return maxTotal;
        }, 100);

        if (maxPercentage === 0) return;

        const nonZeroSliders = sliders.filter(
          (slider, index) =>
            index !== modifiedSliderIndex &&
            slider.adjustable &&
            slider.percentage > 0,
        );

        const adjustableSliders = sliders.filter(
          (slider, index) => index !== modifiedSliderIndex && slider.adjustable,
        );

        // we add/remove percentage points equally across our nonZeroSliders
        const divisor =
          percentageDelta > 0
            ? nonZeroSliders.length
            : adjustableSliders.length;

        // in the case that the amount that we're subtracting leads to a negative
        // number. We add up all those negative differences and subtract from an
        // available slider with that percentage bandwidth
        let missingDifference = nonZeroSliders.reduce((difference, slider) => {
          if (
            divisor > 1 &&
            slider.percentage - percentageDelta / divisor < 0
          ) {
            return difference + slider.percentage - percentageDelta / divisor;
          }
          return difference;
        }, 0);

        return {
          sliders: sliders.map((slider, index) => {
            let newPercentage;
            if (!slider.adjustable) {
              newPercentage = slider.percentage;
            } else if (index === modifiedSliderIndex) {
              newPercentage = Math.min(
                slider.percentage + percentageDelta,
                maxPercentage,
              );
            } else {
              newPercentage =
                slider.percentage -
                Math.min(percentageDelta, maxPercentage) / divisor;
              if (newPercentage + missingDifference > 0) {
                newPercentage += missingDifference;
                missingDifference = 0;
              }
            }

            const clampedPercentage = clamp(newPercentage, 0, 100);
            return {
              ...slider,
              percentage: clampedPercentage,
            };
          }),
        };
      },
    },
  ),
  withHandlers({
    getMonthlyCashback: ({ sliders, monthlyBudget }) => () =>
      reduce(
        sliders,
        (accumulatedBudget, currentCategory) =>
          accumulatedBudget +
          (currentCategory.percentage / 100) *
            monthlyBudget *
            (currentCategory.multiple / 100),
        0,
      ),
  }),
)(_Calculator);
