import React from "react";
import { compose, withHandlers } from "recompose";
import edit from "assets/img/icons/black/light-fill/edit.svg";

import {
  BudgetInputField,
  SpaceBetweenWrapper,
  EditIcon,
  InputWrapper,
  MonthlyLabel,
  MonthlyCashback,
  FlexContainer,
} from "./elements";

const BudgetInput = ({ setNewBudget, monthlyBudget }) => (
  <InputWrapper>
    <SpaceBetweenWrapper>
      <MonthlyLabel>MONTHLY</MonthlyLabel>
      <EditIcon src={edit} />
    </SpaceBetweenWrapper>
    <FlexContainer>
      <MonthlyCashback>$</MonthlyCashback>
      <BudgetInputField
        type="text"
        onChange={setNewBudget}
        value={monthlyBudget.toLocaleString()}
      />
    </FlexContainer>
  </InputWrapper>
);

export default compose(
  withHandlers({
    setNewBudget: ({ setMonthlyBudget }) => (e) => {
      const number = parseInt(e.target.value.replace(/,/g, ""), 10) || 0;
      setMonthlyBudget(Math.min(number, 1000000));
    },
  }),
)(BudgetInput);
