import React from "react";
import { withHandlers, withStateHandlers, compose } from "recompose";
import { round, throttle } from "lodash";
import reset from "assets/img/rewards/reset.svg";

import { formatDollars } from "utils/formatting";

import {
  Wrapper,
  LabelWrapper,
  Label,
  Primary,
  ProgressBar,
  Progress,
  Control,
  Percentage,
  ResetIcon,
} from "./elements";

const _Slider = ({
  startDrag,
  category,
  multiple,
  monthlyBudget,
  percentage,
  progressBarRef,
  controlRef,
  progressRef,
  repositionControlElement,
  endMobileDrag,
  adjustable,
  index,
  toggleAdjustable,
}) => (
  <Wrapper>
    <LabelWrapper>
      <Label>
        <Primary>{`${multiple}X`}</Primary>
        {category}
        {!adjustable && (
          <ResetIcon src={reset} onClick={() => toggleAdjustable(index)} />
        )}
      </Label>
      <Label>{formatDollars(round((monthlyBudget * percentage) / 100))}</Label>
    </LabelWrapper>
    <ProgressBar ref={progressBarRef}>
      <Progress
        adjustable={adjustable}
        width={`${percentage}%`}
        ref={progressRef}
      >
        <Control
          adjustable={adjustable}
          ref={controlRef}
          onMouseDown={startDrag}
          onTouchMove={throttle(repositionControlElement, 50)}
          onTouchEnd={endMobileDrag}
        >
          <Percentage adjustable={adjustable}>
            {`${round(percentage)}%`}
          </Percentage>
        </Control>
      </Progress>
    </ProgressBar>
  </Wrapper>
);

export default compose(
  withStateHandlers({
    progressBarRef: React.createRef(),
    controlRef: React.createRef(),
    progressRef: React.createRef(),
  }),
  withHandlers({
    endDrag: ({ toggleAdjustable, index }) => () => {
      document.onmousemove = null;
      document.onmouseup = null;
      toggleAdjustable(index);
    },
    endMobileDrag: ({ toggleAdjustable, index }) => () => {
      toggleAdjustable(index);
    },
    repositionControlElement: ({
      percentage,
      index,
      updateSliders,
      progressBarRef,
      adjustable,
    }) => (e) => {
      e.preventDefault();
      if (!adjustable) return;
      const lowerBound = progressBarRef.current.getBoundingClientRect().left;
      const upperBound = progressBarRef.current.getBoundingClientRect().right;
      const width = upperBound - lowerBound;
      const x = e.clientX || e.touches[0].clientX;
      const newControlX = Math.max(0, Math.min(x - lowerBound, width));
      const newPercentage = (newControlX / width) * 100;
      updateSliders(index, newPercentage - percentage);
    },
  }),
  withHandlers({
    startDrag: ({ adjustable, endDrag, repositionControlElement }) => (e) => {
      e.preventDefault();
      if (!adjustable) return;
      document.onmouseup = endDrag;
      document.onmousemove = throttle(repositionControlElement, 20);
    },
  }),
)(_Slider);
