import React from "react";
import { withStateHandlers } from "recompose";

import {
  Wrapper,
  LogoWrapper,
  Logo,
  Partner,
  Offer,
  TextWrapper,
} from "./elements";

const Card = ({ offer, hovering, setHovering }) => (
  <Wrapper
    large={offer.large}
    onMouseOver={() => setHovering(true)}
    onFocus={() => setHovering(true)}
    onMouseOut={() => setHovering(false)}
    onBlur={() => setHovering(false)}
    hovering={!offer.large && hovering}
  >
    <LogoWrapper large={offer.large} hovering={hovering}>
      <Logo src={offer.logo} />
    </LogoWrapper>
    <TextWrapper large={offer.large} hovering={hovering}>
      <Partner>{offer.name}</Partner>
      <Offer>{offer.description}</Offer>
    </TextWrapper>
  </Wrapper>
);

export default withStateHandlers(
  {
    hovering: false,
  },
  { setHovering: () => (value) => ({ hovering: value }) },
)(Card);
